import React from "react";
import { Link } from "react-router-dom";

const Nav = (props) => {
    return (
        <nav>
            <Link to={process.env.PUBLIC_URL + "/"}>
                <h1>Magic Shop</h1>
            </Link>
            <ul id="nav-links">
                <Link to={process.env.PUBLIC_URL + "/shop"}>
                    <li>Shop</li>
                </Link>
                <Link to={process.env.PUBLIC_URL + "/cart"}>
                    <li>{`Cart: ${props.cartCount}`}</li>
                </Link>
            </ul>
        </nav>
    );
};

export default Nav;
